
import { defineComponent, computed } from 'vue'

type Props = {
  color: string;
}

export default defineComponent({
  props: {
    color: {
      type: String,
      default: 'gray-600'
    }
  },
  setup (props: Props) {
    const textColorClass = computed((): string => {
      return `text-${props.color}`
    })

    return {
      textColorClass
    }
  }
})
